@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@300;400&display=swap');


.image-container {
  display: flex;
  flex-wrap: wrap;
  font-family: 'Inconsolata', monospace;}

.image {
  width: 33.33%; 
  padding: 10px;
  box-sizing: border-box;
}

.image img {
  width: 100%;
  height: auto;
}

.image p {
  margin-top: 10px;
}


@media (max-width: 768px) {
  .image {
    width: 50%; 
  }
}

@media (max-width: 480px) {
  .image {
    width: 100%;
  }
}





.containera {
  display: flex;
  align-items: center;
  padding: 30px;
}

.imagea {
  flex: 0 0 50%;
  padding-right: 30px;
}

.imagea img {
  width: 100%;
  height: auto;
}

.contenta {
  flex: 0 0 50%;
}


@media (max-width: 768px) {
  .containera {
    flex-direction: column; 
  }

  .image,
  .contenta {
    flex: 1 1 auto; 
    padding-right: 0;
    padding-bottom: 30px;
  }
}
.btn2 {
    
  font-size: 20px;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: 'Source Code Pro', monospace;
}
h2{
  text-align: center;
}
